<template>
  <v-card class="rounded-lg elevation-0">
    <v-list class="pa-0">
      <v-list-item>
        <v-list-item-content>
          <v-text-field v-model="todoDraft"
                        placeholder="Agregar nueva tarea..." solo hide-details flat dense/>
        </v-list-item-content>
        <v-list-item-action>
          <v-btn @click="addTodo"
                 :disabled="!todoDraft"
                 class="text-capitalize font-weight-medium text-small primary elevation-0">
            Agregar
          </v-btn>
        </v-list-item-action>
      </v-list-item>
    </v-list>
    <v-divider/>
    <v-list class="max-h-40 overflow-y-auto">
      <v-list-item v-for="(todo, idx) in todos" :key="idx" @click="todo.done = !todo.done">
        <template v-if="todo.done">
          <v-list-item-avatar class="cursor-pointer" size="30">
            <v-icon class="pa-1 rounded-circle green white--text">
              mdi-check
            </v-icon>
          </v-list-item-avatar>
          <v-list-item-content class="text-body-1 text-gray-900">
            {{ todo.title }}
          </v-list-item-content>
        </template>
        <template v-else>
          <v-list-item-avatar size="30" color="grey lighten-2">
            <v-icon color="white" size="27">
              mdi-checkbox-blank-circle-outline
            </v-icon>
          </v-list-item-avatar>
          <v-list-item-content class="text-body-1 text-gray-900">
            {{ todo.title }}
          </v-list-item-content>
        </template>
      </v-list-item>
    </v-list>
    <div class="pa-5 gray-100 d-flex text-body-2
                font-weight-medium
                text-gray-700 justify-space-between">
      <div>
        <v-chip color="green lighten-2">
          Realizadas: {{ todos.filter(todo => todo.done ).length }}
        </v-chip>
      </div>
      <div>
        <v-chip outlined>
          Pendientes: {{ todos.filter(todo => !todo.done).length }}
        </v-chip>
      </div>
      <div>
        <v-chip>
          Total: {{ todos.length }}
        </v-chip>
      </div>
    </div>
  </v-card>
</template>

<script>
export default {
  name: 'TodoWidget',
  data: () => ({
    todoDraft: '',
    todos: [
      {
        title: 'Tarea #1',
        done: false,
      },
    ],
  }),
  methods: {
    addTodo() {
      this.todos.push({ title: this.todoDraft, done: false });
      this.todoDraft = '';
    },
  },
};
</script>

